import React from 'react';
import {Route,Switch, BrowserRouter} from 'react-router-dom';

//Layouts
import {
  IntroLayout,
  HomeLayout,
} from "./layouts/layouts";

//PAGES
import Intro from './pages/Intro/Intro';
import Home from './pages/Home/Home';
import AboutPage from './pages/AboutPage/AboutPage';
import AboutMe from './pages/AboutMe/AboutMe';
import Works from './pages/Works/Works';
import Skills from './pages/Skills/Skills';


const App = () => {

  return (
    <BrowserRouter>
        <Switch>
          <RouteWrapper exact path="/home" component={Intro} layout={HomeLayout} />
          <RouteWrapper exact path="/" component={Home} layout={IntroLayout} />
          <RouteWrapper exact path="/aboutpage" component={AboutPage} layout={HomeLayout} />
          <RouteWrapper exact path="/aboutme" component={AboutMe} layout={HomeLayout} />
          <RouteWrapper exact path="/works" component={Works} layout={HomeLayout} />
          <RouteWrapper exact path="/skills" component={Skills} layout={HomeLayout} />
        </Switch>
    </BrowserRouter>
  );
};

const RouteWrapper = ({ component: Component, layout: Layout, ...rest }) => {

  return (
    <Route
      {...rest}
      render={(props) => (
        <Layout {...props}>
          <Component {...props} />
        </Layout>
      )}
    />
  );
};

export default App;
