import React from 'react';
import './works.scss';
import {useHistory} from 'react-router-dom';

const WorkCard = ({title, image})=>{
const history = useHistory();

    return (
        <div style={{ background:`url(${image})`, backgroundSize:"100%"}} className="works-cards">
           <div className="works-cards-title">
                <h2>{title}</h2>
           </div> 
        </div>  
    )
};

export default WorkCard;