import './firstmenu.scss';
import React, {useState} from 'react';
import {useHistory} from 'react-router-dom';

import icon1 from './../../assets/macos9-icons/icon-1.png';
import icon2 from './../../assets/macos9-icons/icon-2.png';
import icon3 from './../../assets/macos9-icons/icon-3.png';
import icon4 from './../../assets/macos9-icons/icon-4.png';
import icon5 from './../../assets/macos9-icons/icon-5.png';
import icon6 from './../../assets/macos9-icons/icon-6.png';

const FirstMenu = ({first})=>{
    const history = useHistory()
    const [sub, setSub] = useState(false)
    return (
        <>
        <div className="firstmenu firstmenu-slideDown">
            <div onClick={()=> {
                history.push("/aboutpage")
                first()
            }} className="firstmenu-col">
                <img src={icon1} alt="about this page"></img>
                About This Page
            </div>
            <div onClick={()=> {
                history.push("/aboutme")
                first()
                }}  className="firstmenu-col">
            <img src={icon6} alt="about me"></img>
                About Me 
            </div>
            <div onClick={()=> {
                history.push("/works")
                first()
                }} className="firstmenu-col">
            <img src={icon5} alt="about me"></img>
                Works </div>
            <div 
            onClick={()=> {
                history.push("/skills")
                first()
                }}
            className="firstmenu-col">
            <img src={icon4} alt="about me"></img>
                Skills </div>
            <div 
            onClick={()=> sub ? setSub(false) : setSub(true)}
            className="firstmenu-col" >
            <img src={icon2} alt="about me"></img>
                Contact </div>
        </div>     
        <div className={sub ? "firstmenu-submenu-on " : "firstmenu-submenu-off"}>
 
        </div>
        </>
    )
};

export default FirstMenu;