import { useDrag } from 'react-dnd';
import { ItemTypes } from './ItemTypes';
const style = {
    position: 'absolute',
    padding: '0.5rem 1rem',
    cursor: 'move',
};

export const Box = ({ id, left, top, icon, hideSourceOnDrag, children, changeRoute }) => {
    const [{ isDragging }, drag] = useDrag(() => ({
        type: ItemTypes.BOX,
        item: { id, left, top },
        collect: (monitor) => ({
            isDragging: monitor.isDragging(),
        }),
    }), [id, left, top]);
    if (isDragging && hideSourceOnDrag) {
        return <div ref={drag}/>;
    }

    

    return (
        <div 
        onDoubleClick={() => icon ? changeRoute(children) : ""}
        ref={drag} style={{ ...style, left, top }} >
            <div className="icon-box">
                <div className="icon-box-col">
                    {icon
                        ?(
                            <img width="70px" src={icon} alt={children}></img>
                        ):(
                            <></>
                        )
                    }
                </div>
                <div className="icon-box-col-title">
	                {children}
                </div>
            </div>
		</div>
    );
};
