import React from 'react';
import TopNavBar from './../components/TopNavBar/TopNavBar.jsx';
const IntroLayout = ({ children }) => {
    return (
        <>
            {children}
        </>
    )
};
const HomeLayout = ({ children }) => {
    return (

        <>
            <TopNavBar  />
            {children}
        </>
       
    )
};



export {HomeLayout, IntroLayout}