import axios from 'axios';

//GET USER 
export const GET_ICONS_REQUEST = "GET_ICONS_REQUEST";
export const GET_ICONS_SUCCESS = "GET_ICONS_SUCCESS";
export const GET_ICONS_FAILURE = "GET_ICONS_FAILURE";

//GET ALL GAMES
export const getIcons = () => {

    return (dispatch) => {

        dispatch(getIconsRequest());

        const options = {
            method: 'GET',
            url: `https://api.rawg.io/3ab828ac629f479285310723b1a1c63e/games`,
            params: {
                secret_token: "secretToket",
            },
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        };

        axios.request(options).then(icons => {
            dispatch(getIconsSuccess(icons.data));
        }).catch(error => {
            dispatch(getIconsFailure(error));
        });
    };
};

export const getIconsRequest = () => {
    return {
        type: GET_ICONS_REQUEST,
    };
};
export const getIconsSuccess = (icons) => {
    return {
        type: GET_ICONS_SUCCESS,
        payload: icons
    };
};
export const getIconsFailure = (error) => {
    return {
        type: GET_ICONS_FAILURE,
        payload: error
    };
};
