import { useState } from 'react';
import { Container } from './Container';
import './container.scss';
import ContextMenu from 'react-context-menu';

export const Desktop = () => {
    const [hideSourceOnDrag] = useState(true);

    return (<div id="clickable-area" className="container-desktop">
        <ContextMenu
                className="rigth-menu"
                contextId={'clickable-area'}
                closeOnClickOut={true}
                items={[
                    {
                    label: 'Welcome to my portfolio',
                    },
                    {
                    label: 'Felipe Traina Full Stack Web Developer',
                    }
                ]} />
			    <Container hideSourceOnDrag={hideSourceOnDrag}/>
		    </div>
        );
};