import React from 'react';
import './works.scss';
import {useHistory} from 'react-router-dom';
import WorkCard from './WorkCard.jsx';

//images
import ClimaAppImg from '../../assets/myWork/ClimaApp/screenshot-full-0.png';
import RecipesAppImg from '../../assets/myWork/Recipes/SharedScreenshot.jpg';
import IbmAppImg from '../../assets/myWork/IBM/screenshot.jpg';
import FirebaseLoginAppImg from '../../assets/myWork/FirebaseLogin/SharedScreenshot.jpg';
import BeatStore from '../../assets/myWork/BeatStore/SharedScreenshot.jpg';
import MyPortfolioAppImg from '../../assets/myWork/MyPortfolio/SharedScreenshot.jpg';
import HablameAppImg from '../../assets/myWork/Hablame/Screenshot0.png';
const Works = ()=>{
const history = useHistory();

const worksArray = [
  {
    title: "Clima App",
    image: ClimaAppImg
  },
  {
    title: "Firebase Login",
    image: FirebaseLoginAppImg
  },
  {
    title: "E-commerce",
    image: BeatStore
  },
  {
    title: "Recipes",
    image: RecipesAppImg
  },
  {
    title: "IBM Watson Bot",
    image: IbmAppImg
  },
  {
    title: "Landing Page",
    image: ClimaAppImg
  },
  {
    title: "My Portfolio",
    image: MyPortfolioAppImg
  },
  {
    title: "My Blog",
    image: ClimaAppImg
  },
  {
    title: "Wordpress Magazine",
    image: ClimaAppImg
  },
  {
    title: "Text to Voice",
    image: HablameAppImg
  }
]
    return (
        <div className={"works-page-container-w slideUp"}>
        <div className="works-page-container-w-col-1" >
          <button 
          onClick={()=> history.push("/home")}
          className="works-page-button"></button>
        </div>
        <div className="works-page-container-w-col-2">
          <main className="works-page-container-w-col-2-main">
         { worksArray.map((work, index)=>{
           return <WorkCard title={work.title} image={work.image}/>
         })}
     
            
          </main>
        </div>
      </div>
    )
};

export default Works;