import { useCallback, useState } from "react";
import { useDrop } from "react-dnd";
import { ItemTypes } from "./ItemTypes";
import { Box } from "./Box";
import update from "immutability-helper";
import {useHistory} from "react-router-dom";

import macFinder from './../../assets/desktop-icons/Mac_Finder_23501.png';
import wtsIcon from './../../assets/desktop-icons/3d/whatsapp_182726.png';
import callIcon from './../../assets/desktop-icons/3d/call_icon_182734.png';
import spotyIcon from './../../assets/desktop-icons/3d/spotify_logo_multimedia_icon_182745.png';
import gmailIcon from './../../assets/desktop-icons/3d/google_icon_182721.png';
import folderIcon from './../../assets/desktop-icons/3d/folder_storage_sort_icon_182736.png';
import gIcon from './../../assets/desktop-icons/3d/chat_messages_message_talk_icon_182731.png';
import notesIcon from './../../assets/desktop-icons/3d/note_checklist_182740.png';
import henryIcon from './../../assets/desktop-icons/3d/perspaleta2_0043.png';
import ClockWidget from '../widgets/ClockWidget/ClockWidget';

//
import About from '../../pages/About/About';

const styles = {
  width: "99%",
  height: "100vh",
  border: "none",
  position: "relative",
};

export const Container = ({ hideSourceOnDrag }) => {
  const [whatsapp, setWhatsapp] = useState(false)
  const [gmail, setGmail] = useState(false)
  const [call, setCall] = useState(false)
  const [music, setMusic] = useState(false)
  const [work, setWork] = useState(false)
  const [henry, setHenry] = useState(false)

  const [boxes, setBoxes] = useState({
    1: { top: 20, left: 80, title: "Finder", icon:macFinder, hidden: false },
    2: { top: 180, left: 50, title: "Whatsapp", icon:wtsIcon, hidden: false },
    3: { top: 182, left: 130, title: "Gmail", icon:gmailIcon,hidden: false },
    4: { top: 380, left: 20, title: "Call Me", icon:callIcon, hidden: false },
    5: { top: 382, left: 100, title: "Music", icon:spotyIcon, hidden: false },
    6: { top: 400, left: 320, title: "My Work", icon:folderIcon, hidden: false },
    7: { top: 400, left: 520, title: "Messages", icon:gIcon, hidden: false },
    8: { top: 100, left: 520, title: "Notes", icon:notesIcon, hidden: false },
    9: { top: 300, left: 520, title: "I´m Henry", icon:henryIcon, hidden: false },
    10: { top: 258, left: 781, title: <ClockWidget/>,hidden: false },
    11: { top: 258, left: 681, title: <About/>, hidden: true }
  });

  const history = useHistory()
  const changeRouteHandle = (value, hidden)=>{

    switch (value) {
      case "Finder":
        return history.push("/home")
      case "Whatsapp":
        boxes['11'].hidden ? hideBox(11, false) : hideBox(11, true)
        break;  
      case "Gmail":
        setGmail(true)
        break; 
      case "Call Me":
        setCall(true)
        break; 
      case "Music":
         setMusic(true)
        break;  
      case "My Work":
        setWork(true)
        break;
      case "I´m Henry":
        setHenry(true)
        break;
      default:
        break;
    }

 
  }

  const handleOut = ()=>{
    setWhatsapp(false)
  }
  const moveBox = useCallback(
    (id, left, top) => {
      setBoxes(
        update(boxes, {
          [id]: {
            $merge: { left, top }
          }
        })
      );
    },
    [boxes, setBoxes]
  );
  const hideBox = useCallback(
    (id, hidden) => {
      setBoxes(
        update(boxes, {
          [id]: {
            $merge: { hidden }
          }
        })
      );
    },
    [boxes, setBoxes]
  );

  const [, drop] = useDrop(
    () => ({
      accept: ItemTypes.BOX,
      drop(item, monitor) {
        const delta = monitor.getDifferenceFromInitialOffset();
        const left = Math.round(item.left + delta.x);
        const top = Math.round(item.top + delta.y);
        moveBox(item.id, left, top);
        return undefined;
      }
    }),
    [moveBox]
  );
  return (
    <>
    <div onClick={handleOut} ref={drop} style={styles}>
      {Object.keys(boxes).map((key) => {
        const { left, top, title, icon, hidden } = boxes[key];

        if(left === 681 && hidden){
          return ""
        }else{

          return (
            <Box
              key={key}
              id={key}
              left={left}
              top={top}
              icon={icon}
              hideSourceOnDrag={hideSourceOnDrag}
              changeRoute={changeRouteHandle}
            >
              {title}
            </Box>
          );
        }
      })}
    </div>
    </>
  );
};