import React, {useState} from 'react';
import './topnavbar.scss';
//Components

//import { useHistory } from 'react-router-dom';
import logo from './../../assets/navbar-icons/original-apple-logo-rainbow-transparent.png';
import FirstMenu from '../FirstMenu/FirstMenu';
import DateMenu from '../DateMenu/DateMenu';
import TimeMenu from '../TimeMenu/TimeMenu';

const TopNavBar = ()=>{
    const [firstMenu, setFirstMenu] = useState(false);
    const [dateMenu, setDateMenu] = useState(false);
    const [timeMenu, setTimeMenu] = useState(false);
    const handleFirstMenu = ()=>{
        return firstMenu ? setFirstMenu(false) : setFirstMenu(true)
    }
    const handleDateMenu = ()=>{
        setTimeMenu(false)
        return dateMenu ? setDateMenu(false) : setDateMenu(true)
    }
    const handleTimeMenu = ()=>{
        setDateMenu(false) 
        return timeMenu ? setTimeMenu(false) : setTimeMenu(true)
    }
    const [time, setTime] = useState()

    const fecha = Date.now();
    const hoy = new Date(fecha);

    function intervale(){
        return setTime(new Date().toLocaleTimeString())
      }
      
    setInterval(intervale, 1000);

    // const history = useHistory();

    // const handleClick = ()=>{
    //     return history.push("/home");
    // }
    return (
        <>
        <div className="topnavbar">
            <div className="topnavbar-col-1">
                <div 
                onClick={()=> handleFirstMenu()}
                className={firstMenu ? "topnavbar-menu-icon-active" : "topnavbar-menu-icon"}>
                    <img className={"topnavbar-logo"} src={logo} alt="Felipe Traina"></img>
                </div>
            </div>
            <div className="topnavbar-col-2">
                {/* <h4 className="fadeIn">Felipe Traina Full Stack Web Developer</h4> */}
            </div>
            <div 
            className="topnavbar-col-3">
                    <p>
                        <span
                        onClick={()=> handleDateMenu()}
                        > {hoy.toDateString()} </span>
                     -  <span
                        onClick={()=> handleTimeMenu()}
                     > {time} </span>
                    </p>
            </div>
        </div>
        {firstMenu
        
        ?(
            <FirstMenu first={handleFirstMenu}></FirstMenu>
        ):(
            <>
            </>
        )}
        {dateMenu
        
        ?(
            <DateMenu  />
        ):(
            <>
            </>
        )}
        {timeMenu
        
        ?(
            <TimeMenu  />
        ):(
            <>
            </>
        )}
        </>
    )
};


  
export default TopNavBar;