import {
    GET_ICONS_REQUEST,
    GET_ICONS_SUCCESS,
    GET_ICONS_FAILURE,

} from './desktop.actions';

let initialState = {
    icons: [],
    iconsLoading: true,
    iconsError: "All Good No Errors ",

}

const gamesReducers = (state = initialState, action) => {
    switch (action.type) {
      
        case GET_ICONS_REQUEST:
            return {
                ...state,
                iconsLoading: true
            }
        case GET_ICONS_SUCCESS:
            return {
                ...state,
                iconsLoading: false,
                icons: action.payload
            }
        case GET_ICONS_FAILURE:
            return {
                ...state,
                iconsError: action.payload,
                iconsLoading: false
            }
        default:
            return state;
    }
}

export default gamesReducers;